import {
    Alert,
    Box,
    Button,
    ListItem,
    ListItemButton,
    ListItemIcon,
    ListItemText,
    Switch,
} from "@mui/material";
import mapValues from "lodash/mapValues";
import { useSnackbar } from "notistack";
import { useEffect, useState } from "react";
import { useTranslation } from "react-i18next";
import { useMiddleware } from "../../../../Application";
import useForm from "../../../../utils/useForm";

const DirectForm = () => {
    const { t } = useTranslation(["customers-club", "global"]);

    const { enqueueSnackbar } = useSnackbar();

    const middleware = useMiddleware("Customers-Club.Welcome");

    const [initialValues, setInitialValues] = useState({
        "customers-club:direct_active": false,
    });

    const { register, setValue, getValue, reset, loading, handleSubmit } =
        useForm({
            initialValues,
            handlePost: (data) => middleware.updateOptions(data),
            onSubmit: (response) => {
                enqueueSnackbar(
                    t("global:general.update_successful", "Update successful")
                );
                setInitialValues(response.options);
            },
        });
    const getOptions = async () => {
        const response = await middleware.getOptions();
        setInitialValues(
            mapValues(
                response.options,
                (value, key) => value || initialValues[key]
            )
        );
    };

    useEffect(() => {
        getOptions();
    }, []);

    useEffect(() => {
        reset(initialValues);
    }, [initialValues]);

    return (
        <Box>
            <Box sx={{ mb: 2 }}>
                <Alert color="warning" sx={{ mb: 2 }} severity="warning">
                    قبل از فعال‌سازی لینک دایرکتی، مشخصات کسب و کار خود را از طریق
                    <strong> تنظیمات و تب باشگاه مشتریان</strong>، تکمیل کنید.
                </Alert>
                <ListItem disableGutters disablePadding>
                    <ListItemButton selected>
                        <ListItemText
                            primary={t("فعال‌سازی")}
                            secondary={t(
                                "در صورت غیرفعال‌سازی صفحه لینک دایرکتی بسته خواهد شد."
                            )}
                        />
                        <ListItemIcon>
                            <Switch />
                        </ListItemIcon>
                    </ListItemButton>
                </ListItem>
            </Box>
            <Button
                variant="contained"
                onClick={handleSubmit}
                disabled={loading}
            >
                {t("global:general.save_changes")}
            </Button>
        </Box>
    );
};

export default DirectForm;
