import { Alert, Card, CardContent, Grid, styled } from "@mui/material";
import DOMPurify from "dompurify";
import Markdown from "marked-react";
import { useTranslation } from "react-i18next";

import { useTask } from "../../../../Application";
import ResponsiveContainer from "../../../../components/ResponsiveContainer";
import { TabView } from "../../../../tabs";
import Form from "./Form";

const MarkdownContainer = styled("div")(({ theme }) => ({
    "& h3": {
        marginTop: 0,
    },
}));

const Page = () => {

    const { t } = useTranslation("customers-club");

    const task = useTask('Customers-Club.direct');

    const GUIDE = `
### نکات آموزشی

در پیامک‌هایی که می‌فرستید اگر قصد ارسال بیش از یک لینک برای مشتریان خود دارید، با کمک لینک دایرکتی پنج لینک را در یک لینک خلاصه کنید.

مثلا در یک کمپین پیامکی با هدف افزایش مخاطبان در شبکه‌های اجتماعی می‌خواهید لینک تمامی شبکه‌های اجتماعی که در آن‌ها فعال هستید را در پیامک خود قرار دهید که برای این کار کافی‌ست لینک دایرکتی خود را در همین قسمت تنظیم کنید.

از آنجایی که در بیو اینستاگرام فقط می‌توان از یک لینک استفاده کرد کاربرد دیگر لینک دایرکتی همین است و می‌توان آن را در بیوی اینستاگرام، تلگرام و… قرار داد که از حداکثر پنج لینک پشتیبانی می‌کند.

عنوان لینک: اسم لینکی که قرار می‌دهید را وارد کنید. (مثل وبسایت) رنگ: برای عنوان این لینک یک رنگ مشخص انتخاب کنید. آدرس لینک: لینک دلخواه خود را وارد نمایید. (مثل لینک وبسایت)

حذف: برای حذف کردن هر لینکی که تنظیم کرده‌اید کافی ست روی دکمه قرمز رنگ "حذف" کلیک کنید.

لینک بعدی: برای افزودن لینک جدید روی این دکمه کلیک کنید.

در این بخش حداکثر لینک‌هایی که می‌توانید قرار دهید ۵ لینک است. بعد از تنظیم اسم، رنگ و آدرس هر لینک روی دکمه ذخیره بزنید تا تنظیمات شما ثبت شود.

بعد از تنظیم و ذخیره لینک‌ها، بر روی دکمه آبی رنگ "لینک دایرکتی" کلیک کنید و سپس روی کپی بزنید و آن را در هر جایی که می‌خواهید قرار دهید.
    `;

    return (
        <TabView {...task}>
            <ResponsiveContainer>
                <Card variant="outlined" sx={{ mb: 3 }}>
                    <CardContent sx={{ p: 3 }}>
                        <Grid container spacing={5}>
                            <Grid item xs={12} sm={6}>
                                <Alert color="info" severity="info">
                                    <MarkdownContainer>
                                        <Markdown breaks={true}>
                                            {DOMPurify.sanitize(GUIDE)}
                                        </Markdown>
                                    </MarkdownContainer>
                                </Alert>
                            </Grid>
                            <Grid item xs={12} md={6}>
                                <Form />
                            </Grid>
                        </Grid>
                    </CardContent>
                </Card>
            </ResponsiveContainer>
        </TabView>
    )
}


export default Page;